html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-size: 1rem;
}

:root {
  --yellow:hsl(47, 88%, 63%);
  --white:hsl(0, 0%, 100%);
  --gray:hsl(0, 0%, 50%);
  --black:hsl(0, 0%, 7%);
}

body {
  font-family: "Figtree", sans-serif;
}

.container {
  padding: 1.5625rem;
}